import { Component } from "@angular/core";
import { IonicPage } from "ionic-angular";

@IonicPage({
  name: 'calendar'
})
@Component({
  selector: 'page-calendar',
  templateUrl: 'calendar.html',
})

export class CalendarPage {

  date: string;
  type: 'string'; // 'string' | 'js-date' | 'moment' | 'time' | 'object'
  constructor() { }
 
  onChange($event) {
    console.log($event);
  }
}



